
/*  Forms */
/* ==================================================== */


form {
	@extend %clearfix;
	@include copy(regular);
	position: relative;
	width: 100%;
	display: block;
	background: transparent;

	.modal & {
		align-self: center;
	    margin: auto;
    	width: 85%;//75%;
	    max-width: 768px;
	}
}



fieldset {
	width: 100%;
	display: block;
	margin: 0 auto;
	padding: 0;
	font-size: 1rem;
	background: transparent;
	border: 0 none;
	@extend %clearfix;

	.modal & {
		position: relative;
	}

	&:not(:first-of-type) {
		margin-top: calc(#{$space-tiny} * 1.75);

		&.column-3 {
		}
	}
}


label {
	display: block;
	margin: 0 0 0.751rem;
	padding: 0;
	@include font-size(18px);
	@include font-size(16px);
	@include line-height(1.1);
	text-transform: capitalize;
	color: $grey-md;
	color: lighten($blue-dk, 2.5%);

	&.radio-check {
		display: inline;
		font-weight: normal;
	}

	span {
		color: $blue-dk;
		// font-weight: bold;
		font-size: 1.25rem;
	}
}


input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
textarea,
select {
	@include font-size(16px);
	@include letter-spacing(tiny);
	position: relative;
	//display: block;
	width: 100%;
	padding: 0.75rem 1rem;
	//color: $grey-md;
	background: $white;
	appearance: none;
	border: 0 none;
	outline: 0 none;
	border: 1px solid $blue-lt;
	border-radius: 2px;

	&:hover {
		color: $blue-lt;
		border: 1px solid $green-dk;
	}

	&:focus {
		color: $blue-dk;
		border: 1px solid $blue-dk;
	}

	&::placeholder,
	&::-webkit-input-placeholder {
		color: $grey-md;
		opacity: 1;
	}
}

input[type="text"] {
	.modal & {
		@include copy(light);
		@include font-size(28px);
		width: calc(100% - 56px);
		padding: 1.25rem 1.5rem;
		border: 0 none;
		outline: 0 none;
	}
}


textarea {
	height: 10rem;
}


select {
	min-width: 25%;
	// width: auto;
	margin: 0;
	border-radius: 3px;
}


option {
	padding: 0.25rem 0 0;
	border: 0 none;
	outline: 0 none;
}


fieldset.radio ul {
	@extend %clearfix;
	margin-top: 0.25rem;
	list-style-type: none;
}


fieldset.radio ul li {
	float: left;
	margin: 0 2rem 0.25rem 0;
}


fieldset.radio ul li:last-child {
	margin: 0;
}


p.form-help {
	@include font-size(15px);
	display: inline-block;
	margin: 0.125rem 0 0.25rem;
	color: #999;
}

p.form-data {
	@include font-size(18px);
	display: inline-block;
	margin: 0.125rem 0 0.75rem;
}


input[type="submit"] {
	appearance: none;
	border: 0 solid transparent;
	outline: 0 solid transparent;
	cursor: pointer;

	&:hover,
	&:focus {
	}

	&:active {
	}

	.modal & {
		position: absolute;
	    top: calc(#{$space-tiny} * 0);
	    right: 0;
	    width: 40px;
	    height: 72px;
	    opacity: 0;
	    z-index: 9;
	}
}


.search-icon {
	.modal & {
		position: absolute;
		top: calc(#{$space-tiny} * 1);
		right: 0;
		align-self: center;
		width: 40px;
		text-align: center;
		background: transparent;
		border: 0;
		outline: 0;
		cursor: pointer;
		z-index: 8;

		svg {
			width: 32px;
			margin: auto;
		}

		path {
	        fill: $white;
	    }
	}
}


img.ajax-loader {
	max-height: 16px;
	max-width: 16px;
	display: none;
}





