
/*  Flex-box grid */
/* ==================================================== */


$system: 12;
$gutter: $space-2-5;


@mixin grid-settings($cols, $gut: null, $split: null) {
    flex-grow: $cols;
    $void: calc(#{$gutter} * #{$gut});
    $sum: calc(100% - #{$void});
    width: calc(#{$sum} / #{$split});
    max-width: calc(#{$sum} / #{$split});
}


%columns {
    @include flexbox();
    @include flex-direction(column);
    clear: both;
    margin: 0;

    &.columns-wrap {
        @include flex-wrap(wrap);
    }

    &.columns-nowrap {
        @include flex-wrap(nowrap);
    }

    &:not(:first-of-type) {
        margin-top: 3rem;
    }

    @media #{$mq-1024} {
        @include flex-direction(row);
    }
}

%column {
    flex: 1 1 auto;

    &:not(:first-of-type) {
        margin-top: $gutter;
    }

    @media #{$mq-1024} {
        &:not(:first-of-type) {
            margin-left: $gutter;
        }
    }
}


%column-1 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(1,11,12);

        &:not(:first-of-type) {
            margin-top: unset;
        }
    }
}

%column-2 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(2,5,6);

        &:not(:first-of-type) {
            margin-top: unset;
        }

        &:nth-child(6n+7) {
            margin-left: 0;
        }

        &:nth-child(n+7) {
            margin-top: $gutter;
        }
    }
}

%column-3 {
    @extend %column;
    @include grid-settings(12,0);

    @media #{$mq-1024} {
        @include grid-settings(3,3,4);

        &:not(:first-of-type) {
            margin-top: unset;
        }

        &:nth-child(4n+5) {
            margin-left: 0;
        }

        &:nth-child(n+5) {
            margin-top: $gutter;
        }
    }
}

