        &:not(:first-of-type) {
            margin-top: unset;
        }

        &:nth-child(3n+4) {
            margin-left: 0;
        }

        &:nth-child(n+4) {
            margin-top: $gutter;
        }
    }
}

%column-5 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(5,2,2);

        &:not(:first-of-type) {
            margin-top: unset;
        }
    }
}

%column-6 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(2,1,2);

        &:not(:first-of-type) {
            margin-top: unset;
        }

        &:nth-child(2n+3) {
            margin-left: 0;
        }

        &:nth-child(n+3) {
            margin-top: $gutter;
        }
    }
}

%column-7 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(7,1);

        &:not(:first-of-type) {
            margin-top: unset;
        }
    }
}

%column-8 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(8,1);

        &:not(:first-of-type) {
