        }
    }
}

%column-9 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(9,1);

        &:not(:first-of-type) {
            margin-top: unset;
        }
    }
}

%column-10 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(10,1);

        &:not(:first-of-type) {
