        }
    }
}

%column-11 {
    @extend %column;

    @media #{$mq-1024} {
        @include grid-settings(11,1);

        &:not(:first-of-type) {
            margin-top: unset;
        }
    }
}

%column-12 {
    @extend %column;
    margin-left: 0;

    @media #{$mq-1024} {
        @include grid-settings(12,0);
        margin-left: 0;

        &:not(:first-of-type) {
            margin-top: unset;
        }

        &:not(:first-of-type) {
            margin-left: 0;
        }
    }
}



.columns {
    @extend %columns;

    &.columns-clear {
        margin-top: 0;
    }
}

.column {
    @extend %column;
}

@for $col from 1 through 12 {
    .column-#{$col} {
        @extend %column-#{$col};
    }
}

.column-hide {
    display: none;

    @media #{$mq-1024} {
        display: block
	}
}

