/* Base HTML */
html {

}


/* Base body */
body {
    @include flexbox();
	@include flex-wrap(nowrap);
    @include flex-direction(column);
    position: relative;
    min-height: 100vh;
    font-size: var(--text-base-size);
    line-height: var(--body-line-height);
    background: $white;

    &.body-no-scroll {
		overflow: hidden;
	}
}

